import React, { useState, useEffect, useCallback } from 'react'
import CloudinaryImage from '../../atoms/CloudinaryImage/CloudinaryImage'
import useLanguageStore from '../../../stores/languageStore'
import { galleryProjects, projectCategories, getProjectsByCategory, getAllProjects } from '../../../constants/galleryData'

const ProjectGallery = () => {
  const { currentLanguage } = useLanguageStore()
  const [activeCategory, setActiveCategory] = useState('all')
  const [filteredProjects, setFilteredProjects] = useState(galleryProjects)
  const [selectedImage, setSelectedImage] = useState(null)
  const [isLoading, setIsLoading] = useState(false)
  const [lightboxImageLoading, setLightboxImageLoading] = useState(false)
  const [showLightboxLoader, setShowLightboxLoader] = useState(false)
  const [touchStart, setTouchStart] = useState(null)
  const [touchEnd, setTouchEnd] = useState(null)
  const [mobileSlideIndex, setMobileSlideIndex] = useState(0)
  const [mobileTouchStart, setMobileTouchStart] = useState(null)
  const [mobileTouchEnd, setMobileTouchEnd] = useState(null)

  const content = {
    en: {
      title: 'Discover the perfect combination of elegance and comfort\nin landscape design',
      subtitle: 'Create a place where unforgettable memories are born. Contact us - and we will help bring your dreams to life.',
      filterAll: 'All Projects',
      loadMore: 'Load More',
      close: 'Close'
    },
    ua: {
      title: 'Відкрийте ідеальне поєднання елегантності й комфорту\nв ландшафтному дизайні',
      subtitle: 'Створіть місце, де народжуються незабутні спогади. Зверніться до нас - і ми допоможемо втілити ваші мрії.',
      filterAll: 'Всі проекти',
      loadMore: 'Завантажити більше',
      close: 'Закрити'
    },
    de: {
      title: 'Entdecken Sie die perfekte Kombination aus Eleganz und Komfort\nim Landschaftsdesign',
      subtitle: 'Schaffen Sie einen Ort, an dem unvergessliche Erinnerungen entstehen. Kontaktieren Sie uns - und wir helfen Ihnen, Ihre Träume zu verwirklichen.',
      filterAll: 'Alle Projekte',
      loadMore: 'Mehr laden',
      close: 'Schließen'
    },
    pl: {
      title: 'Odkryj idealne połączenie elegancji i komfortu\nw projektowaniu krajobrazu',
      subtitle: 'Stwórz miejsce, gdzie rodzą się niezapomniane wspomnienia. Skontaktuj się z nami - a pomożemy Ci spełnić Twoje marzenia.',
      filterAll: 'Wszystkie projekty',
      loadMore: 'Załaduj więcej',
      close: 'Zamknij'
    }
  }

  const currentContent = content[currentLanguage] || content.en

  useEffect(() => {
    if (activeCategory === 'all') {
      setFilteredProjects(getAllProjects())
    } else {
      setFilteredProjects(getProjectsByCategory(activeCategory))
    }
    setMobileSlideIndex(0) // Reset slide index when category changes
  }, [activeCategory])

  const handleCategoryChange = (categoryId) => {
    setIsLoading(true)
    setActiveCategory(categoryId)

    // Simulate loading for smooth transition
    setTimeout(() => {
      setIsLoading(false)
    }, 300)
  }

  const openLightbox = (project, index) => {
    // Disable lightbox on mobile devices (≤768px) since image size is the same
    if (window.innerWidth <= 768) {
      return
    }

    setLightboxImageLoading(true)
    setShowLightboxLoader(false)

    // Show loader after 1 second delay
    const loaderTimeout = setTimeout(() => {
      if (lightboxImageLoading) {
        setShowLightboxLoader(true)
      }
    }, 1000)

    setSelectedImage({
      project,
      index,
      allProjects: filteredProjects,
      loaderTimeout
    })
  }

  const closeLightbox = useCallback(() => {
    if (selectedImage?.loaderTimeout) {
      clearTimeout(selectedImage.loaderTimeout)
    }
    setSelectedImage(null)
    setLightboxImageLoading(false)
    setShowLightboxLoader(false)
  }, [selectedImage])

  const navigateLightbox = useCallback((direction) => {
    if (!selectedImage) return

    // Clear previous timeout
    if (selectedImage.loaderTimeout) {
      clearTimeout(selectedImage.loaderTimeout)
    }

    setLightboxImageLoading(true)
    setShowLightboxLoader(false)

    // Show loader after 1 second delay
    const loaderTimeout = setTimeout(() => {
      if (lightboxImageLoading) {
        setShowLightboxLoader(true)
      }
    }, 1000)

    const currentIndex = selectedImage.index
    const totalImages = selectedImage.allProjects.length
    let newIndex

    if (direction === 'prev') {
      newIndex = currentIndex > 0 ? currentIndex - 1 : totalImages - 1
    } else {
      newIndex = currentIndex < totalImages - 1 ? currentIndex + 1 : 0
    }

    setSelectedImage({
      ...selectedImage,
      project: selectedImage.allProjects[newIndex],
      index: newIndex,
      loaderTimeout
    })
  }, [selectedImage, lightboxImageLoading])

  // Swipe handlers for mobile
  const handleTouchStart = (e) => {
    setTouchEnd(null)
    setTouchStart(e.targetTouches[0].clientX)
  }

  const handleTouchMove = (e) => {
    setTouchEnd(e.targetTouches[0].clientX)
  }

  const handleTouchEnd = () => {
    if (!touchStart || !touchEnd) return

    const distance = touchStart - touchEnd
    const isLeftSwipe = distance > 50
    const isRightSwipe = distance < -50

    if (isLeftSwipe && selectedImage) {
      navigateLightbox('next')
    }
    if (isRightSwipe && selectedImage) {
      navigateLightbox('prev')
    }
  }

  // Mobile slider swipe handlers
  const handleMobileTouchStart = (e) => {
    setMobileTouchEnd(null)
    setMobileTouchStart(e.targetTouches[0].clientX)
  }

  const handleMobileTouchMove = (e) => {
    setMobileTouchEnd(e.targetTouches[0].clientX)
  }

  const handleMobileTouchEnd = () => {
    if (!mobileTouchStart || !mobileTouchEnd) return

    const distance = mobileTouchStart - mobileTouchEnd
    const isLeftSwipe = distance > 50
    const isRightSwipe = distance < -50

    if (isLeftSwipe) {
      // Next slide
      setMobileSlideIndex(prev =>
        prev < filteredProjects.length - 1 ? prev + 1 : 0
      )
    }
    if (isRightSwipe) {
      // Previous slide
      setMobileSlideIndex(prev =>
        prev > 0 ? prev - 1 : filteredProjects.length - 1
      )
    }
  }

  const handleKeyPress = useCallback((e) => {
    if (!selectedImage) return

    if (e.key === 'Escape') {
      closeLightbox()
    } else if (e.key === 'ArrowLeft') {
      navigateLightbox('prev')
    } else if (e.key === 'ArrowRight') {
      navigateLightbox('next')
    }
  }, [selectedImage, closeLightbox, navigateLightbox])

  // Preload adjacent images
  useEffect(() => {
    if (selectedImage) {
      const currentIndex = selectedImage.index
      const totalImages = selectedImage.allProjects.length
      const cloudName = process.env.REACT_APP_CLOUDINARY_CLOUD_NAME || 'dsunqfo3g'

      // Preload next image
      const nextIndex = currentIndex < totalImages - 1 ? currentIndex + 1 : 0
      const nextProject = selectedImage.allProjects[nextIndex]
      if (nextProject) {
        const nextImg = new Image()
        nextImg.src = `https://res.cloudinary.com/${cloudName}/image/upload/w_1600,h_1067,c_fit,f_auto,q_auto/${nextProject.publicId}`
      }

      // Preload previous image
      const prevIndex = currentIndex > 0 ? currentIndex - 1 : totalImages - 1
      const prevProject = selectedImage.allProjects[prevIndex]
      if (prevProject) {
        const prevImg = new Image()
        prevImg.src = `https://res.cloudinary.com/${cloudName}/image/upload/w_1600,h_1067,c_fit,f_auto,q_auto/${prevProject.publicId}`
      }
    }
  }, [selectedImage])

  useEffect(() => {
    if (selectedImage) {
      document.addEventListener('keydown', handleKeyPress)
      document.body.style.overflow = 'hidden'
    } else {
      document.removeEventListener('keydown', handleKeyPress)
      document.body.style.overflow = 'unset'
    }

    return () => {
      document.removeEventListener('keydown', handleKeyPress)
      document.body.style.overflow = 'unset'
    }
  }, [selectedImage, handleKeyPress])

  return (
    <section className="project-gallery-section">
      <div className="project-gallery-container">
        {/* Header */}
        <div className="project-gallery-header">
          <h2 className="project-gallery-title">
            {currentContent.title}
          </h2>
          <p className="project-gallery-subtitle">
            {currentContent.subtitle}
          </p>
        </div>

        {/* Category Filters */}
        <div className="project-gallery-filters">
          <button
            onClick={() => handleCategoryChange('all')}
            className={`filter-button ${activeCategory === 'all' ? 'active' : ''}`}
          >
            {currentContent.filterAll}
          </button>
          {projectCategories.map(category => (
            <button
              key={category.id}
              onClick={() => handleCategoryChange(category.id)}
              className={`filter-button ${activeCategory === category.id ? 'active' : ''}`}
            >
              {category.name[currentLanguage] || category.name.en}
            </button>
          ))}
        </div>

        {/* Desktop Gallery Grid */}
        <div className="project-gallery-grid desktop-gallery">
          {isLoading ? (
            <div className="gallery-loading">
              <div className="loading-spinner"></div>
            </div>
          ) : (
            filteredProjects.map((project, index) => (
              <div
                key={project.id}
                className="gallery-item gallery-item-clickable"
                onClick={() => openLightbox(project, index)}
              >
                <CloudinaryImage
                  publicId={project.publicId}
                  alt={project.alt?.[currentLanguage] || project.alt?.en || `Granite paving project ${project.id}`}
                  width={600}
                  height={400}
                  className="gallery-image"
                />
                <div className="gallery-item-overlay">
                  <div className="gallery-item-info">
                    <span className="project-category">
                      {projectCategories.find(cat => cat.id === project.category)?.name[currentLanguage] ||
                       projectCategories.find(cat => cat.id === project.category)?.name.en}
                    </span>
                  </div>
                </div>
              </div>
            ))
          )}
        </div>

        {/* Mobile Slider */}
        <div className="mobile-gallery-slider">
          {isLoading ? (
            <div className="gallery-loading">
              <div className="loading-spinner"></div>
            </div>
          ) : filteredProjects.length > 0 ? (
            <div
              className="mobile-slide-container"
              onTouchStart={handleMobileTouchStart}
              onTouchMove={handleMobileTouchMove}
              onTouchEnd={handleMobileTouchEnd}
            >
              <div className="mobile-slide-wrapper">
                <CloudinaryImage
                  publicId={filteredProjects[mobileSlideIndex]?.publicId}
                  alt={filteredProjects[mobileSlideIndex]?.alt?.[currentLanguage] || filteredProjects[mobileSlideIndex]?.alt?.en || `Granite paving project ${filteredProjects[mobileSlideIndex]?.id}`}
                  width={800}
                  height={600}
                  className="mobile-slide-image"
                />
              </div>

              {/* Slide Indicators */}
              <div className="mobile-slide-indicators">
                <span className="slide-counter">
                  {mobileSlideIndex + 1} / {filteredProjects.length}
                </span>
                <div className="slide-dots">
                  {filteredProjects.map((_, index) => (
                    <div
                      key={index}
                      className={`slide-dot ${index === mobileSlideIndex ? 'active' : ''}`}
                      onClick={() => setMobileSlideIndex(index)}
                    />
                  ))}
                </div>
              </div>
            </div>
          ) : null}
        </div>

        {/* Lightbox Modal */}
        {selectedImage && (
          <div className="lightbox-overlay" onClick={closeLightbox}>
            <div
              className="lightbox-container"
              onClick={e => e.stopPropagation()}
              onTouchStart={handleTouchStart}
              onTouchMove={handleTouchMove}
              onTouchEnd={handleTouchEnd}
            >
              <button
                className="lightbox-close"
                onClick={closeLightbox}
                aria-label={currentContent.close}
              >
                <svg width="24" height="24" viewBox="0 0 24 24" fill="none">
                  <path d="M18 6L6 18M6 6l12 12" stroke="currentColor" strokeWidth="2" strokeLinecap="round"/>
                </svg>
              </button>

              <button
                className="lightbox-nav lightbox-prev"
                onClick={() => {
                  console.log('Previous button clicked');
                  navigateLightbox('prev');
                }}
                aria-label="Previous image"
                style={{
                  position: window.innerWidth > 1024 ? 'fixed' : 'absolute',
                  left: window.innerWidth > 1024 ?
                    (window.innerWidth >= 1920 ? 'calc(50vw - 800px)' :
                     window.innerWidth >= 1440 ? 'calc(50vw - 700px)' :
                     window.innerWidth >= 1280 ? 'calc(50vw - 600px)' : 'calc(50vw - 500px)') :
                    (window.innerWidth > 768 ? '1rem' : '0.5rem'),
                  top: '50%',
                  transform: 'translateY(-50%)',
                  width: window.innerWidth > 768 ? '3.5rem' : '2.5rem',
                  height: window.innerWidth > 768 ? '3.5rem' : '2.5rem',
                  background: 'rgba(0, 0, 0, 0.8)',
                  border: '2px solid rgba(255, 255, 255, 0.9)',
                  borderRadius: '50%',
                  color: 'white',
                  zIndex: 1002,
                  cursor: 'pointer',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center'
                }}
              >
                <svg width="24" height="24" viewBox="0 0 24 24" fill="none">
                  <path d="M15 18l-6-6 6-6" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                </svg>
              </button>

              <div className="lightbox-image-container">
                {lightboxImageLoading && showLightboxLoader && (
                  <div className="lightbox-image-loader">
                    <div className="lightbox-loading-spinner"></div>
                  </div>
                )}
                <CloudinaryImage
                  publicId={selectedImage.project.publicId}
                  alt={selectedImage.project.alt?.[currentLanguage] || selectedImage.project.alt?.en || `Granite paving project ${selectedImage.project.id}`}
                  width={2400}
                  height={1600}
                  className="lightbox-image"
                  responsive={true}
                  onLoad={() => {
                    if (selectedImage?.loaderTimeout) {
                      clearTimeout(selectedImage.loaderTimeout)
                    }
                    setLightboxImageLoading(false)
                    setShowLightboxLoader(false)
                  }}
                  onError={() => {
                    if (selectedImage?.loaderTimeout) {
                      clearTimeout(selectedImage.loaderTimeout)
                    }
                    setLightboxImageLoading(false)
                    setShowLightboxLoader(false)
                  }}
                />
              </div>

              <button
                className="lightbox-nav lightbox-next"
                onClick={() => {
                  console.log('Next button clicked');
                  navigateLightbox('next');
                }}
                aria-label="Next image"
                style={{
                  position: window.innerWidth > 1024 ? 'fixed' : 'absolute',
                  right: window.innerWidth > 1024 ?
                    (window.innerWidth >= 1920 ? 'calc(50vw - 800px)' :
                     window.innerWidth >= 1440 ? 'calc(50vw - 700px)' :
                     window.innerWidth >= 1280 ? 'calc(50vw - 600px)' : 'calc(50vw - 500px)') :
                    (window.innerWidth > 768 ? '1rem' : '0.5rem'),
                  top: '50%',
                  transform: 'translateY(-50%)',
                  width: window.innerWidth > 768 ? '3.5rem' : '2.5rem',
                  height: window.innerWidth > 768 ? '3.5rem' : '2.5rem',
                  background: 'rgba(0, 0, 0, 0.8)',
                  border: '2px solid rgba(255, 255, 255, 0.9)',
                  borderRadius: '50%',
                  color: 'white',
                  zIndex: 1002,
                  cursor: 'pointer',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center'
                }}
              >
                <svg width="24" height="24" viewBox="0 0 24 24" fill="none">
                  <path d="M9 18l6-6-6-6" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                </svg>
              </button>

              <div className="lightbox-info">
                <div className="lightbox-counter">
                  {selectedImage.index + 1} / {selectedImage.allProjects.length}
                </div>
                {window.innerWidth <= 768 && (
                  <>
                    <div className="swipe-hint">
                      ← Swipe →
                    </div>
                    <button
                      className="mobile-exit-button"
                      onClick={closeLightbox}
                      aria-label={currentContent.close}
                    >
                      {currentContent.close}
                    </button>
                  </>
                )}
              </div>
            </div>
          </div>
        )}
      </div>
    </section>
  )
}

export default ProjectGallery